  background: none;
  color: inherit;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.border-button {
  border: 1px solid white;
  border-radius: 5px;
  padding: 15px 54px;
  transition: color .5s ease-in-out;
  background: none !important;
}

.border-button svg {
  width: 100%;
  height: 100%;
  fill: white !important;
}

.border-button:hover {
  color: #292B33;
}

.button {
  border-radius: 5px;
  padding: 12px 35px;
  background: #292B33;
  color: white;
  display: block;
  width: fit-content;
  font-size: 16px;
  position: relative;
  overflow: hidden;
}

.button .bg {
  position: absolute;
  display: block;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: width .5s ease-in-out, height .5s ease-in-out;
  z-index: 1;
}

.button .bg svg {
  fill: #37D078;
  width: 100%;
  height: 100%;
}

.button:hover .bg {
  width: 300%;
  height: 700px;
}

.button .text {
  position: relative;
  z-index: 2;
  line-height: 1em;
  font-family: 'Circe Reg', sans-serif;
}

.underline-button {
  transition: color .4s ease-in-out;
  overflow: hidden;
  width: fit-content;
  white-space: nowrap;
}

.underline-button::after {
  content: '';
  display: flex;
  width: 100%;
  height: 1px;
  background: white;
  margin-top: 3px;
  font-size: 18px;
  transition: background .5s ease-in-out;
}

.underline-button::after.opacity {
  opacity: 0.3;
}

.underline-button.black::after {
  background: black;
}

.underline-button:hover {
  color: #37D078;
}

.underline-button:hover::after {
  animation: button-line 1s ease-in-out;
  background: #37D078;
}

@keyframes button-line {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(100%);
  }
  51% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.scroll-down {
  position: absolute;
  bottom: 3%;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 90px;
  background: rgba(255, 255, 255, 0.4);
  opacity: 1;
  overflow: hidden;
}

.scroll-down:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  height: 100%;
  width: 100%;
  animation: scrolling 2.5s infinite cubic-bezier(0.645, 0.045, 0.355, 1);
}

@keyframes scrolling {
  0% {
    transform: translateY(-100%);
  }
  70% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(100%);
  }
}

@media (max-width: 1375px) {
  .scroll-down {
    height: 70px;
  }
}

